import type { Theme } from '@emotion/react';

import { useDesignSystemTheme } from '../Hooks';
import type { IconProps } from '../Icon';
import { CircleIcon, CircleOfflineIcon, CircleOutlineIcon } from '../Icon';

export type ResourceStatus = 'online' | 'disconnected' | 'offline';
export interface ResourceStatusIndicatorProps {
  status: ResourceStatus;
}

const STATUS_TO_ICON: {
  [key in ResourceStatus]: ({ theme, ...props }: IconProps & { theme: Theme }) => JSX.Element;
} = {
  online: ({ theme, ...props }) => <CircleIcon color="success" {...props} />,
  disconnected: ({ theme, ...props }) => <CircleOutlineIcon css={{ color: theme.colors.grey500 }} {...props} />,
  offline: ({ theme, ...props }) => <CircleOfflineIcon css={{ color: theme.colors.grey500 }} {...props} />,
};

export const ResourceStatusIndicator = (props: ResourceStatusIndicatorProps) => {
  const { status } = props;
  const { theme } = useDesignSystemTheme();
  const StatusIcon = STATUS_TO_ICON[status];

  return <StatusIcon theme={theme} />;
};
